@font-face{
    font-family: 'Lato';
    src: url('../static/fonts/Lato-Regular/fonts/Lato-Regular.eot');
    src: url('../static/fonts/Lato-Regular/fonts/Lato-Regular.eot?#iefix') format('embedded-opentype'),
    url('../static/fonts/Lato-Regular/fonts/Lato-Regular.woff') format('woff'),
    url('../static/fonts/Lato-Regular/fonts/Lato-Regular.ttf') format('truetype');
    font-weight: 400;
    font-style:normal;
    font-stretch:normal;
    font-display:swap;
}
